//region imports icons
import walletIcon from '../../assets/icons/wallet-icon.svg'
import refillIcon from '../../assets/icons/bitcoin-icon.svg'
import sendIcon from '../../assets/icons/send-icon.svg'
import historyIcon from '../../assets/icons/history-icon.svg'
import settingIcon from '../../assets/icons/settings-icon.svg'
import faqIcon from '../../assets/icons/faq-icon.svg'
//endregion

export default {
    data() {
        return {
            navs: [
                {
                    heading: '',
                    children: [
                        { icon: 'wallet-icon', title: ' ', routeName: 'wallet'}
                    ]
                },
                {
                    heading: 'Transaction',
                    children: [
                        { icon: 'refill-icon', title: ' ', routeName: 'refill'},
                        { icon: 'send-icon', title: ' ', routeName: 'send'}
                    ]
                },
                {
                    heading: ' ',
                    children: [
                        { icon: 'history-icon', title: '', routeName: 'history'},
                        { icon: 'setting-icon', title: '', routeName: 'setting'},
                        { icon: 'faq-icon', title: '/', routeName: 'faq'}
                    ]
                }
            ]
        }
    },
    computed: {
        
    },
    props: {
        
    },
    methods: {
        
    },
    components: {
        settingIcon, historyIcon, faqIcon, walletIcon, refillIcon, sendIcon
    },
}


